html, body {
  font-family: Arial, Helvetica, sans-serif;
  height: 100%;
  width: 100%;
  font-size: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  padding: 0;
  margin: 0;
  background-color: #122c34;
}

@media only screen and (max-width: 500px) {
  html, body {
      font-size: 90%;
  }
}

@media only screen and (max-width: 400px) {
  html, body {
      font-size: 80%;
  }
}

header span {
  color: #ffffff;
  font-size: 2em;
  padding: 12px;
  box-sizing: border-box;
  display: inline-block;
}

.App {
  height: 100%;
}

footer {
  position: fixed;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #ffffff;
  padding: 6px;
  box-sizing: border-box;
}

footer div {
  display: flex;
  align-items: center;
}

footer div span {
  padding-right: 6px;
}

.ad {
  width: 30em;
  height: 8em;
  position: absolute;
  left: 50%;
  top: 80%;
  transform: translate(-50%, -50%);
}