.clock-container {
    position: absolute;
    width: 100%;
    top: 50%;
    transform: translateY(-50%);
}

.clock h1 {
    font-size: 4em;
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;
    padding-bottom: 1em;
    color: #44CFCB;
    margin: 0;
}

.clock h2 {
    font-size: 1em;
    padding: 0.6em;
    color: #4EA5D9;
    font-weight: bold;
    margin: 0;
    text-align: center;
}

.clock h5 {
    text-align: center;
    margin: 0;
    color: #4EA5D9;
}

.clock p {
    color: #4EA5D9;
    text-align: center;
}

.row {
    display: flex;
    justify-content: center;
    max-width: 100%;
}

.col {
    padding: 0.6em;
    background-color: #224870;
    border-radius: 5px;
    margin: 0 5px;
}

.col span {
    display: block;
}

.col .number {
    background-color: white;
    font-size: 3em;
    text-align: center;
    border-radius: 3px;
    box-sizing: border-box;
    width: 1.75em;
    padding: 0.25em 0;
    font-weight: bold;
}

.col .text {
    text-align: center;
    padding-top: 10px;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: bold;
    color: #44CFCB;
}